//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START

@font-face {
	@include fontdefault(normal, 400, 'Intro');
	@include fontpath('Intro', 'Intro', 'Intro'); }

@font-face {
	@include fontdefault(italic, 800, 'Intro');
	@include fontpath('Intro-Black-Italic', 'Intro-Black-Italic', 'Intro-Black-Italic'); }

@font-face {
	@include fontdefault(normal, 700, 'ProximaNova');
	@include fontpath('ProximaNova-Bold', 'ProximaNova-Bold', 'ProximaNova-Bold'); }

@font-face {
	@include fontdefault(normal, 400, 'ProximaNova');
	@include fontpath('ProximaNova-Regular', 'ProximaNova-Regular', 'ProximaNova-Regular'); }
